// , id
export function ajaxSaveAddresses (params, callback) {
	if (!params.name) {
		uni.showToast({title: '', icon: 'none', position: 'bottom'});
	} else if (!params.mobile) {
		uni.showToast({title: '', icon: 'none', position: 'bottom'});
	} else if (!params.address && !params.doorAddress) {
		uni.showToast({title: '', icon: 'none', position: 'bottom'});
	} else {
		let data = Mock.mock({
			'msg': '',
			'code': 200,
			'result': 'adrressId'
		});
		uni.showLoading({title: LOADINGTEXT});
		setTimeout(function () {uni.hideLoading(); callback && callback(data);}, Math.floor(Math.random() * dure));
	}
}

// 
export function ajaxPlaceOrder (pms, callback) {
	let goods = pms.goods || [];
	let params = {
		userid: pms.openId,
		// 
		name: pms.name,
		// 
		mobile: pms.mobile,
		// 
		address: pms.address,
		// 
		doorAddress: pms.doorAddress,
		goods: [],
		money: pms.money || 0,
		count: pms.count || 0
	};
	for (let i = 0; i < goods.length; i++) {
		params.goods.push({
			id: goods[i].id,
			name: goods[i].name,
			count: goods[i].count,
			rprice: goods[i].rprice,
			unit: goods[i].unit
		});
	}
	if (!params.goods || params.goods.length === 0) {
		uni.showToast({title: '', icon: 'none', position: 'bottom'});
	} else if (!params.name) {
		uni.showToast({title: '', icon: 'none', position: 'bottom'});
	} else if (!params.mobile) {
		uni.showToast({title: '', icon: 'none', position: 'bottom'});
	} else if (!params.address && !params.doorAddress) {
		uni.showToast({title: '', icon: 'none', position: 'bottom'});
	} else {
		console.log('====');
		console.log(params);
		let data = Mock.mock({
			'msg': '',
			'code': 200
		});
		uni.showLoading({title: LOADINGTEXT});
		setTimeout(function () {uni.hideLoading(); callback && callback(data);}, Math.floor(Math.random() * dure));
	}
}

